// - - - - - - - - - - Foundations

@import "_reset";
@import "_mixins";
@import "_plugins";



// - - - - - - - - - - Variables


// Colors

$background-color: #EDDCD2;
$background-alt-color: #EDC787;
$border-color: #BAB285;

$text-dark-color: #373737;
$text-medium-color: #5B5B5B;
$text-light-color: #123C69;

$accent-color: #BAB285;

$error-color: #D64541;


// Fonts

$body-font: "Georgia", serif;
$title-font: "Georgia", serif;

$regular-weight: 400;
$bold-weight: 600;


// Typography

$sitetitle-small: 18px;
$sitetitle-medium: 19px;
$sitetitle-large: 20px;
$sitetitle-weight: 600;
$sitetitle-spacing: 0em;

$sitetagline-small: 12px;
$sitetagline-medium: 13px;
$sitetagline-large: 14px;
$sitetagline-weight: 400;
$sitetagline-spacing: 0em;
$sitetagline-lineheight: 1.5;

$menu-small: 11px;
$menu-medium: 13px;
$menu-large: 15px;
$menu-weight: 600;
$menu-spacing: 0em;

$h1-small: 16px;
$h1-medium: 17px;
$h1-large: 18px;
$h1-weight: 600;
$h1-spacing: -0.02em;
$h1-lineheight: 1.2;

$h2-small: 16px;
$h2-medium: 17px;
$h2-large: 18px;
$h2-weight: 600;
$h2-spacing: 0em;
$h2-lineheight: 1.5;

$h3-small: 14px;
$h3-medium: 15px;
$h3-large: 16px;
$h3-weight: 200;
$h3-spacing: 0em;
$h3-lineheight: 1.5;

$h4-small: 14px;
$h4-medium: 15px;
$h4-large: 16px;
$h4-weight: 200;
$h4-spacing: 0em;
$h4-lineheight: 1.6;

$h5-small: 12px;
$h5-medium: 13px;
$h5-large: 14px;
$h5-weight: 400;
$h5-spacing: 0em;
$h5-lineheight: 1.6;

$h6-small: 12px;
$h6-medium: 13px;
$h6-large: 14px;
$h6-weight: 300;
$h6-spacing: 0em;
$h6-lineheight: 1.6;

$p-small: 12px;
$p-medium: 13px;
$p-large: 14px;
$p-weight: 200;
$p-spacing: 0em;
$p-lineheight: 1.6;

$blockquote-small: 20px;
$blockquote-medium: 30px;
$blockquote-large: 40px;
$blockquote-weight: 300;
$blockquote-spacing: 0em;
$blockquote-lineheight: 1.6;

$button-weight: 600;
$button-spacing: 0em;


// Logo
$logo-width: 20px;


// Grid item
$grid-spacing: 10px;



// - - - - - - - - - - Global styles

@import "_basic";

@import "_includes/_header";
@import "_includes/_footer";

@import "_includes/_content";
@import "_includes/_socials";
@import "_includes/_gallery";
@import "_includes/_syntax";
@import "_includes/_contact-form";



// - - - - - - - - - - Section styles

@import "_includes/_listing";


